.p-news-card {
  width: 100%;
  height: 100%;
  background: transparent;

}
.p-news-card  a {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.p-news-card a:hover {
  .p-news-card__figure img {
    transform: scale(1.3) translate(-40%, -40%);

  }
}

.p-news-card__figure {
  width: 100%;
  position: relative;
  overflow: hidden;
  
  }
  
  .p-news-card__figure::before {
    padding-top: 209 / 335 * 100%; //画像縦 / 横
    display: block;
    content: "";
    
    @include mq(tab) {
      padding-top: 319 / 518 * 100%; //画像縦 / 横
      
    }
  
  }
  
  .p-news-card__figure img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
    transition: all .3s;
  }

.p-news-card__body {
 padding: rem(10) 0;
}

  
 .p-news-card__title {
   font-size: rem(12);
   line-height: 1.5;

   @include mq(tab) {
     font-size: rem(14);
   }
 }

  .p-news-card__copy{
    display: none;
  }


.p-news-card__footer{
  display: flex;
  align-items: center;
  // margin-top: auto;
  
}

.p-news-card__category {
  padding: rem(2) rem(14);
  background-color: $btn-color;
  color: #fff;
  font-size: rem(12);
  position: absolute;
  top: 5%;
  left: rem(-7);

}
.p-news-card__category::before {
  position: absolute;
  top: 0;
  display: block;
  height: 11px;
  content: '';
  border: 11.5px solid $btn-color;
  right: rem(-5);
  border-right-width: rem(5);
  border-right-color: transparent;
}



.p-news-card__time {
 font-size: rem(12);
}