
// https://wpqw.jp/snippet/form-reset/
@charset "utf-8";

fieldset,
legend,
button {
	margin: 0;
	padding: 0;
	border: none;
	border-radius: 0;
	outline: none;
	background: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

input[type="date"],
input[type="datetime-local"],
input[type="month"],
input[type="time"] {
	margin: 0;
	padding: 4px;
	border: 1px solid #ccc;
	outline: none;
	font-size: 16px;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="url"],
input[type="search"],
input[type="number"],
input[type="datetime"],
input[type="week"],
textarea,
select {
	// margin-right: 0;
	// margin-left: 0;
	// padding: 4px;
	// border: 1px solid #ccc;
	// border-radius: 0;
	outline: none;
	// background: none;
	// font-size: 16px;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

textarea {
	width: 100%;
	height: 100px;
	overflow: auto;
}

select {
	padding-right: 30px;
	background-image: url("data:image/png;base64,");
	background-repeat: no-repeat;
	background-size: 12px 12px;
	background-position: right 8px center;
}

.select {
	display: inline-block;
	position: relative;
	overflow: hidden;
	background: #fff;
}
.select select {
	width: 100%;
	padding-right: 24px;
	border: 1px solid #ccc;
	background: none;
	text-overflow: "";
	cursor: pointer;
}
.select::before {
	position: absolute;
	top: 13px;
	right: 8px;
	width: 0;
	height: 0;
	padding: 0;
	content: "";
	border-left: 6px solid transparent;
	border-right: 6px solid transparent;
	border-top: 6px solid #666;
	pointer-events: none;
}

