  font-weight: 700;
  transition: all .3s;
}

.c-btn-base__link::before {
  content: "";
  display: block;
  position: absolute;
  right: 1rem;
  top: 50%;
  width: 0.625rem;
  height: 0.625rem;
  border-top: 2px solid #fff;
  border-right: 2px solid #fff;
  -webkit-transform: translateY(-50%) rotate(45deg);
  transform: translateY(-50%) rotate(45deg);
  z-index: 10;
}

.c-btn-base__link::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background: transparent;
  border-right: 1px solid #222222;
  border-bottom: 1px solid #222222;
  position: absolute;
  bottom: -0.3125rem;
  right: -0.3125rem;
  transition: all .3s;
}

.c-btn-base__link:hover {
  transform: translate(0.3125rem, 0.3125rem);
}

.c-btn-base__link:hover::after {
  display: none;
}

