  cursor: pointer;
  display: inline-block;
  padding: 0.3125rem 0.9375rem 0.375rem 1.5625rem;
  font-size: 1rem;
  position: relative;
}

input[type="radio"] + span::before {
  -webkit-transform: translateY(-50%);
  background: #fff;
  border: 1px solid #382620;
  border-radius: 50%;
  content: "";
  display: block;
  height: 1rem;
  left: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 1rem;
}

input[type="radio"] + span::after {
  -webkit-transform: translateY(-50%);
  background: #F07317;
  border: 1px solid transparent;
  border-radius: 50%;
  content: "";
  height: 0.5rem;
  left: 0.25rem;
  opacity: 0;
  padding: 0.125rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.3s ease 0s;
  width: 0.5rem;
}

